<template>
  <v-alert
    text
    dense
    color="indigo"
    icon="mdi-information-outline"
    border="left"
    v-if="all"
  >
    <span v-if="first">
      Sie mssen eine valide Eingabe einfgen um fortfahren zu knnen.<br />
      Bitte besuchen Sie die
      <a href="https://ai-overflow.github.io/yaml-documentation" target="_blank"
        >Dokumentation</a
      >
      fr weitere Informationen.
    </span>
    <span v-if="second">
      Sie mssen die Verbindung
      <code>{{ yamlData ? yamlData.entryPoint : "ERROR" }}</code>
      abschicken um fortfahren zu knnen.
    </span>
  </v-alert>
</template>

<script>
export default {
  props: {
    validationResult: Object,
    connectionData: Object,
    description: String,
    currentStep: Number,
    yamlData: Object,
  },
  computed: {
    first: function () {
      return (
        (!this.validationResult ||
          !this.validationResult.valid ||
          this.description.length == 0) &&
        this.currentStep == 1
      );
    },
    second: function () {
      return (
        (!this.yamlData ||
          !this.yamlData.entryPoint ||
          !this.connectionData ||
          !this.connectionData[this.yamlData.entryPoint] ||
          !this.connectionData[this.yamlData.entryPoint].success) &&
        this.currentStep == 2
      );
    },
    all: function () {
      return this.first || this.second;
    },
  },
};
</script>

